    p(data-aos="fade-down").mb-5 Con lo anterior, puede generarse el cuestionamiento, qu es la venta?, para Westreicher (2020) son la entrega de un determinado bien o servicio bajo un precio estipulado o convenido y a cambio de una contraprestacin econmica en forma de dinero por parte de un vendedor o proveedor.

    .row.justify-content-center.align-items-center.mb-5         
      .col-lg-6
        p(data-aos="fade-down") Otros autores tambin presentan otros conceptos y definiciones, para Heller (s. f.), es el cambio de productos y servicios por dinero. Desde el mbito legal, se trata de la transferencia de la posesin de un bien a otro consumidor, a cambio de dinero y, finalmente, desde el mbito contable y financiero, la venta es el costo total pagado por productos o servicios prestados.

        p(data-aos="fade-down") Para la realizacin de la venta debe tenerse en cuenta varias etapas y a partir de ah desencadenan el proceso transaccional para el intercambio de bienes. La venta tiene varias etapas, definidas por el grado de interaccin del vendedor con el cliente. Estas etapas son: etapa de preventa, etapa de ventas y etapa de posventa, las cuales se analizarn ms adelante.

