    p(data-aos="fade-down") La venta es el proceso transaccional que se efecta entre un comprador y un vendedor. Estos roles pueden ser ejercidos por empresas o personas con otras empresas y personas. Las estrategias de negociacin y la forma cmo se desarrolla la venta dependen del sector, el tipo de producto o servicio y las diversas formas de pago. Entre los tipos de ventas se tienen:

