        p(data-aos="fade-down").mb-0 Tomadores de rdenes internos: o vendedores de pago inmediato, aquellos que reciben el pago y entregan la mercanca. Tambin aplica al telemercadeo que toma pedidos por telfono. 

      .col-lg-4.bg9.text-white.p-4(data-aos="fade-down")
