        p(data-aos="fade-down").mb-0 Personal de entrega: son aquellos que solo realizan la entrega del producto en el domicilio o en el punto de venta, de acuerdo con la distribucin de funciones. 

      .col-lg-4.bg8.text-white.p-4(data-aos="fade-right")
        img.img-a.img-t.mb-4(data-aos="zoom-in")(src='@/assets/curso/temas/img45.png' alt='')
