        p(data-aos="fade-down").mb-5 Los vendedores #[b captadores de rdenes] son los vendedores que responden por una cuota de ventas, realizan el proceso de la venta, identifican nuevos prospectos, realizan persuasin y negociacin. En este grupo se identifican dos subgrupos, los que estn en la denominada lnea de fuego y los que sirven de apoyo a ventas.
        
