    #t_2_2.titulo-segundo.color-acento-contenido(data-aos="zoom-in-left")
      h2 2.2 Proceso de la venta
    .row.justify-content-start.align-items-center.mb-3        
      .col-auto.d-none.d-lg-block.p-0.ps-3
        img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img50.png', alt='')       
      .col-lg-11
        p(data-aos="fade-down").mb-0 Se define como el proceso de venta a cada una de las etapas antes, durante y despus de que se realiza la transaccin. No es una camisa de fuerza seguir cada uno de los pasos en orden estricto, pues depende del tipo de producto o servicio que se est ofreciendo, aun as, se pueden identificar siete momentos durante la relacin vendedor-cliente, a saber:  
    .bg11.p-4.mb-5
      LineaTiempoC.color-acento-contenido(text-small)
        .row.justify-content-center.align-items-center(titulo="Prospeccin")
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img51.png', alt='')      
          .col-lg-6
            h5 Prospeccin 
            p En esta etapa se deben buscar a los clientes potenciales, comenzando con la identificacin de personas que llegan al producto a travs de diferentes canales, como el sitio web, las redes sociales, comentarios en blogs, ventas en el punto de venta, entre otros. As mismo, pueden encontrarse otras plataformas que han servido de contacto. 

            p No todas las personas que lleguen a la empresa, se convertirn en clientes, pues un prospecto es todo usuario que puede convertirse en un potencial comprador del producto o usuario del servicio, es decir, aquellos que poseen la capacidad econmica para comprar y recomprar el producto, as mismo, aquellos que pueden incidir en la decisin de compra.

            p Con los prospectos identificados, corresponde elaborar una base de datos o listado que permita organizarlos.  

        .row.justify-content-center.align-items-center(titulo="Acercamiento")
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img52.png', alt='')      
          .col-lg-6
            h5 Acercamiento 
            p Con el listado organizado o base de datos, se debe comenzar con un conocimiento profundo de los clientes potenciales. Se debe encontrar la forma de familiarizarse con ellos, identificar sus necesidades, hbitos de compra, intereses, entre otros. 

            p Dentro del proceso de segmentacin, hay actividades que pueden apoyar ese conocimiento profundo, mediante encuestas, llamadas telefnicas, consulta personal, entre otros. Es importante obtener datos bsicos como nombre, edad, ciudad de vivienda, profesin, intereses, entre otros. 

        .row.justify-content-center.align-items-center(titulo="Calificacin")
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img53.png', alt='')      
          .col-lg-6
            h5 Calificacin 
            p En el proceso de calificacin se decide si el prospecto puede ser o no un cliente potencial. Durante la consulta, se puede identificar si el prospecto podra comprar el producto analizando sus intereses y presupuesto para la compra. 

            p Tambin es importante identificar si el prospecto solo est interesado (atencin), ya ha consultado acerca del producto (investigacin), est a punto de comprarlo (decisin) o ya compr por exploracin u otra razn (accin).

        .row.justify-content-center.align-items-center(titulo="Presentacin de la oferta")
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img54.png', alt='')      
          .col-lg-6
            h5 Presentacin de la oferta 
            p Una vez recopilada toda la informacin de los clientes potenciales, se hace necesario presentarles una oferta adecuada con sus necesidades e intereses y que presente los beneficios del producto o servicio que puede adquirir. 

            p La oferta puede enviarse por alguno de los canales cercanos al pblico para generar un contacto y comenzar con la fase de negociacin. 

            p Es importante realizar un seguimiento al envo de la oferta para verificar que el cliente la haya recibido y analizado con el tiempo suficiente para conocerla y asimilarla. 

        .row.justify-content-center.align-items-center(titulo="Negociacin")
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img55.png', alt='')      
          .col-lg-6
            h5 Negociacin 
            p En esta etapa del proceso, ya el cliente recibi la propuesta y tiene la informacin del producto. En esta etapa, el cliente es posible que haya hecho indagaciones por cuenta propia, comparaciones, entrevistas o consultas, que le sirvan para ampliar la informacin del producto y es posible que pueda presentar objeciones, preguntas o inquietudes respecto de la oferta, lo que permitir al vendedor, realizar contraofertas que mejoren los beneficios de la primera versin.

            p Lo primero que debe hacerse es escuchar al cliente y sus necesidades; y luego explorar esas preocupaciones y encontrar soluciones para reestructurar la propuesta. Algunos clientes manifestarn que no tienen dinero o que en el momento no se encuentran interesados, lo que debe identificarse como una oportunidad para mejorar las condiciones de la oferta presentada inicialmente.

        .row.justify-content-center.align-items-center(titulo="Cierre de la venta")
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img56.png', alt='')      
          .col-lg-6
            h5 Cierre de la venta
            p El proceso de cierre de la venta es la actividad en la cual el cliente, finalmente, adquiere el producto. Aunque no termina all, es importante aclarar otras condiciones, tales como las condiciones de entrega o los plazos de pago. 

            p Es poco comn, pero puede presentarse que un cliente pague por el producto y desista de la compra, cuando observa que el producto no llegar a tiempo o que se han cambiado otras condiciones de la negociacin, por tanto, el vendedor deber estar acompaando el proceso, an despus de que el cliente tenga el producto en su poder.

            p La comunicacin con el cliente resulta una herramienta muy importante para sellar esta fase del proceso y mantenerlo equilibrado. 

        .row.justify-content-center.align-items-center(titulo="Servicio posventa")
          .col-lg-6
            img.img-a.img-t(src='@/assets/curso/temas/img57.png', alt='')      
          .col-lg-6
            h5 Servicio posventa 
            p Esta fase del proceso tiene un objetivo muy claro y es fidelizar al cliente para que realice compras recurrentes del producto. Mantener un cliente puede resultar ms costoso que un cliente nuevo, pero eso debe significar que el cliente antiguo genera mayores ingresos gracias a la recompra del producto. 

            p Entre las actividades que componen el servicio posventa estn el envo de correos electrnicos, llamadas peridicas de seguimiento, encuestas o consultas de satisfaccin, ofrecer mayores beneficios con la recompra o la recomendacin, entre otras.

    p(data-aos="fade-down").mb-5 Los pasos anteriores consolidarn las bases para fortalecer una relacin de confianza y que ms adelante permita ofrecerle al cliente otros productos. El proceso de venta no siempre se desarrolla en un orden lgico, son los clientes quienes condicionan este relacionamiento, y es una competencia del vendedor identificar qu debe hacer en cada una de ellas.           

