      h2 2.3 Estrategia y tcnicas de ventas     

    p(data-aos="fade-down").mb-3 La estrategia de ventas son acciones a mediano o largo plazo que buscan el cumplimiento de los objetivos del plan de mercadeo, plan de ventas o en s, cualquier accin que emprenda la organizacin, conlleva la ejecucin de estrategias. En este sentido, una estrategia es un conjunto de decisiones sobre actividades que la empresa debe ejecutar para alcanzar un objetivo planteado.

