            i.fas.fa-quote-right 
        .row.justify-content-start.align-items-center.mb-4        
          .col-auto.d-none.d-lg-block.p-0.ps-3
            img.img-a.img-t(data-aos="zoom-in")(src='@/assets/curso/temas/img59.png', alt='')       
