            p(data-aos="fade-down").mb-0 En ventas se establece una meta y luego se toman decisiones de cmo alcanzar esa meta y tener rentabilidad, el equipo de ventas o fuerza de ventas est orientado al cumplimiento de esa meta o cuota de ventas. Las diferentes acciones que se tomen para lograr el objetivo de ventas vienen en armona con las realizadas en el plan de mercadeo de la empresa.                     
        h5(data-aos="fade-down").text-center Para implementar adecuadamente estrategias de ventas, la empresa debe presentar:
        img.img-a.img-t.my-3(data-aos="zoom-in")(src='@/assets/curso/temas/img60.svg', alt='')         

